import React, { useEffect, useState } from 'react'
import Recipe from './components/Recipe'


function App() {

  const APP_ID = 'd9de3991'

  const [recipes, setRecipes] = useState([])
  const [search, setSearch] = useState('')
  const [query, setQuery] = useState('chicken')

  useEffect(() => {
    getRecipes()
  }, [query])

  const getRecipes = async () => {
    const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${process.env.REACT_APP_API_KEY}`)
    const data = await response.json()
    console.log(data)
    setRecipes(data.hits)
  }

  const getSearch = e => {
    e.preventDefault()
    setQuery(search)
    setSearch('')
  }

  const searchStyle={
    width:'60px',
    height:'25px',
    border:'2px solid gray',
    outline:'none',
    borderRadius:'0 3px 3px 0'
  }
  const inputStyle={
    width:'160px',
    height:'23px',
    border:'1px solid gray', 
    outline:'none',
    borderRadius:'3px 0 0 3px'
  }

  return (
    <div className="App">
      <h1>Recipe App</h1>
      <form onSubmit={getSearch}>
        <input style={inputStyle} type="text" value={search} onChange={(e) => setSearch(e.target.value)} />
        <button style={searchStyle}>Search</button>
      </form>
      <br />
      <br />
      <div className="recipes-container">
        {
          recipes.map(recipe => {
            return (
              <Recipe
                key={recipe.recipe.label}
                title={recipe.recipe.label}
                calories={recipe.recipe.calories}
                image={recipe.recipe.image}
                ingredients={recipe.recipe.ingredients}
              />
            )
          })
        }
      </div>
    </div>
  );
}

export default App;
